.section.wysiwyg-block {
    padding: 30px 0;
}
/* If this organism directly follows a form, remove top padding */
.section.form-basic + .section.wysiwyg-block {
    padding-top: 0;
}
.section.wysiwyg-block .container {
    padding: 0 15px;
}
.section.wysiwyg-block .text,
    .section.wysiwyg-block p,
    .section.wysiwyg-block a {
    font-size: 15px;
    line-height: 1.8;
}
.section.wysiwyg-block .text {
    width: 100%;
}
.section.wysiwyg-block .text a[href^=tel] {
    display: block;
    font-size: 20px;
    color: $blue;
}
@media (min-width: $(desktop)) {
    .section.wysiwyg-block {
        padding: 90px 0;
    }
    .section.wysiwyg-block .container {
        padding: 0;
    }
    .section.wysiwyg-block .text,
        .section.wysiwyg-block p,
        .section.wysiwyg-block a {
        font-size: 18px;
        line-height: 2.3;
    }
    .section.wysiwyg-block .text a[href^=tel] {
        font-size: 23px;
    }
}
